<template>
    <div class="wrap components-page">
        <div class="wrapB">

            <h1>Style Component</h1>

            <h2>form</h2>
            <div class="wrap">
                <!-- input type=text -->
                <InputComponent inputValue="email" :errorText="error.email"
                                :enterInput="enterInput"
                                placeholder=" ." label=""/>

                <!-- input type=password -->
                <InputComponent inputValue="password" :errorText="error.password"
                                :enterInput="enterInput"
                                placeholder=" ." label="" password="true"/>

                <!-- textarea -->
                <TextareaComponent inputValue="contents" placeholder=" ." label="" maxLength=300></TextareaComponent>

                <!-- form -->
                <SelectComponent :options="options"/>
                <CheckboxComponent label=" ." checkValue="agree"/>
            </div>

            <h2>switch</h2>
            <div class="wrap">
                <SwitchComponent/>
            </div>

            <h2>button</h2>
            <div class="wrap">
                <!-- button -->
                <LargeButton text="  " :isBackground="true"/>
                <LargeButton text="  " :isBackground="false"/>
                <SmallButton text="" :isBackground="true"/>
                <SmallButton text="" :isBackground="false"/>
                <HalfButton :buttons="buttons"/>
            </div>

            <h2>header</h2>
            <div class="wrap">
                <!-- header -->
                <HeaderComponent headerTitle=" " :isSearch="true"/>
                <HeaderComponent headerTitle=" " :isBack="true"/>
                <HeaderComponent headerTitle=" " :isBack="true" rightText="" isDisabled="true"/>
                <HeaderComponent headerTitle=" " :isBack="true" rightText=""/>
                <HeaderComponent headerTitle=" " :isBack="true" :isSearch="true"/>
            </div>

            <h2>tab</h2>
            <div class="wrap">
                <!-- tab -->
                <TabComponent tabTitle="" />
                <TabComponent tabTitle="" :isActive="true"/>
                <TabComponent tabTitle="" :isActive="true" count="23"/>
            </div>

            <h2>keyword</h2>
            <div class="wrap">
                <!-- keyword -->
                <KeywordDelComponent keywordTitle="" />
                <KeywordAddComponent />
            </div>

            <h2>curation</h2>
            <div class="wrap">
                <!-- curation -->
                <CurationComponent curationTitle=" " />
            </div>

            <h2>toast</h2>
            <div class="wrap">
                <!-- toast -->
                <ToastComponent toastTitle="." />
                <ToastComponent toastTitle="500 Error" :isError="true"/>
                <ToastComponent toastTitle="." :isCancel="true"/>
            </div>



        </div>
    </div>
</template>

<script>
    import InputComponent from '../components/common/Input'
    import TextareaComponent from '../components/common/Textarea'
    import LargeButton from '../components/common/ButtonLarge'
    import SmallButton from '../components/common/ButtonSmall'
    import HalfButton from '../components/common/ButtonHalf'
    import SelectComponent from '../components/common/Select'
    import CheckboxComponent from '../components/common/Checkbox'
    import SwitchComponent from '../components/common/Switch'
    import HeaderComponent from '../components/common/Header'
    import TabComponent from '../components/common/Tab'
    import KeywordDelComponent from '../components/common/KeywordDel'
    import KeywordAddComponent from '../components/common/KeywordAdd'
    import CurationComponent from '../components/common/Curation'
    import ToastComponent from '../components/common/Toast'
    import '../assets/css/components.scss'

    export default {
        name: "components",
        components: {
            InputComponent,
            TextareaComponent,
            LargeButton,
            SmallButton,
            HalfButton,
            SelectComponent,
            CheckboxComponent,
            SwitchComponent,
            HeaderComponent,
            TabComponent,
            KeywordDelComponent,
            KeywordAddComponent,
            CurationComponent,
            ToastComponent,
        },
        data: () => {
            return {
                email:"",
                password:"",
                contents:"",
                error: {
                    email: "",
                    password: "",
                },
                buttons: [
                    {
                        title: '',
                        highlight: true,
                        onClick: () => {
                            console.log('')

                        }
                    },
                    {
                        title: '',
                        onClick: () => {
                            console.log('')
                        }
                    }
                ],
                options: [
                    {
                        value:"option1",
                        title:"1",
                    },
                    {
                        value:"option2",
                        title:"2",
                    },
                ]
            }
        },
        methods:{
            enterInput(e,type) {
                if(type==='email') {
                    this.email = e;
                } else if(type==="password") {
                    this.password = e;
                }
            }
        },
    }
</script>

